import React from 'react';

import "./item.css";

const Item = ({contactItem, removeContact=f=>f, selectContact=f=>f}) =>
    <div className="contact-item">
        <div className="contact-item__actions">
            <button onClick={selectContact} title=""><span className="glyphicon glyphicon-cog"/></button>
            <button onClick={removeContact} title=""><span className="glyphicon glyphicon-trash"/></button>
        </div>
        <div className="row">
            <div className="col-sm-3">
                <p>{contactItem.name}</p>
            </div>
            <div className="col-sm-3">
                <p>.: {contactItem.phone}</p>
                <p>: {contactItem.mail}</p>
            </div>
            <div className="col-sm-3">
                <p>: {contactItem.country}</p>
                <p>: {contactItem.city}</p>
                <p>: {contactItem.street}</p>
            </div>
            <div className="col-sm-2">
                <a href={contactItem.site} className="contact-item__site">{contactItem.site}</a>
            </div>
        </div>
    </div>;

export default Item;
