import React, {Component} from 'react';

import { CreatorContainer } from '../Creator/CreatorContainer';
import FindContact from '../../components/FindContact/FindContact';
import List from '../../components/List/List';

class ContactList extends Component {
    render() {
        let {contactList,
            removeContact=f=>f,
            selectContact=f=>f } = this.props;
        return (
            <div className="contact-list">
                <CreatorContainer />
                <FindContact  />
                {contactList.length >= 1 ?
                    <List contactList={contactList} removeContact={removeContact} selectContact={selectContact}/>:
                    <div className="container">
                        <h4>   .</h4>
                    </div>
                }
            </div>
        )
    }
}
// }({ contactList, removeContact=f=>f, selectContact=f=>f })



export default ContactList;
